.header-container {
  width: 100%;
  height: 100px;
  display: block;
  /* background-color: rgba(0, 89, 135, 0.8); */
  background-image: linear-gradient(to right, rgb(0, 89, 135), rgb(0, 99, 148));
  border-bottom: 1px solid rgba(244, 217, 12, 0.6);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  align-items: center;
  justify-content: center;
}

.header-menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(43, 43, 43, 0.97);
  display: block;
  /* overflow: hidden; */
  z-index: 999;
}

.close-menu-button {
  float: right;
  margin-top: 120px;
  margin-right: 20px;
  border: none;
  background-color: #005987;
}

.header-menu ul {
  width: 90%;
  margin: 100px 0;
  padding-left: 10%;
  display: flex;
  flex-direction: column;
  list-style-type: none;
  justify-content: space-evenly;
  z-index: 999;
}

.header-menu ul a {
  text-decoration: none;
  color: inherit;
}

.header-menu ul li {
  margin: 5px 0;
  color: rgb(245, 245, 245);
  font-family: lemon-milk;
  font-size: 24px;
  text-transform: uppercase;
}

.header-menu-active {
  color: #f4d90c !important;
}

.header-menu ul li .menu-phone {
  border: none;
  background-color: #005987;
  color: #f4d90c;
  font-family: lemon-milk;
  text-align: center;
  line-height: 50px;
  width: 250px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.menu-phone h5 {
  font-size: 14px;
  letter-spacing: 0.3em;
  margin-left: 10px;
}

.header-menu ul li .menu-donate {
  border: none;
  background-color: #f4d90c;
  color: #005987;
  font-family: lemon-milk;
  text-align: center;
  line-height: 50px;
  width: 250px;
  letter-spacing: 0.3em;
  font-weight: 600;
}

.menu-active {
  color: #f4d90c !important;
}

.header-nav-bar {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  /* padding: 0 20px 0 0; */
  box-sizing: border-box;
}

.header-nav-bar a {
  text-decoration: none;
  list-style-type: none;
}

.header-logo-container {
  height: 90%;
  margin: auto 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.trp-logo-text {
  opacity: 0;
  width: 130px;
  text-align: center;
  color: #f4d90c;
  font-weight: lighter;
  font-family: lemon-milk;
  font-size: 14px;
  animation-name: lightsOn2;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-delay: 1.5s;
  animation-timing-function: ease-out;
}

@keyframes lightsOn2 {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.trp-logo-text span {
  font-weight: bold;
  font-size: 16px;
}

.trp-logo-container {
  width: 80px;
  height: 80px;
  justify-content: center;
  align-items: center;
}

.trp-logo-container svg {
  width: 100%;
  margin: 0 auto;
}

svg {
  opacity: 0;
  animation-name: lightsOn2;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
  animation-delay: 0.5s;
  animation-timing-function: ease-out;
  border: 1px solid rgba(244, 217, 12, 0.2);
  box-shadow: 0 1px 2.2px rgba(0, 0, 0, 0.017), 0 1px 5.3px rgba(0, 0, 0, 0.02),
    0 1px 10px rgba(0, 0, 0, 0.03), 0 2px 10px rgba(0, 0, 0, 0.03),
    0 4px 10px rgba(0, 0, 0, 0.03), 0 10px 10px rgba(0, 0, 0, 0.05);
}

#path3777,
#path3761,
#path3773,
#path3771,
#path3767,
#path3765,
#path3763 {
  fill: #005987 !important;
}

#path3775 {
  fill: #a6abaa !important;
}

#path3769 {
  fill: #828282 !important;
}

#path3773 {
  fill: #005987 !important;
  animation-name: lightsOn;
  animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-delay: 1.5s;
  animation-timing-function: ease-out;
}

@keyframes lightsOn {
  from {
    fill: #005987;
  }

  to {
    fill: #f4d90c;
  }
}

.header-items-container {
  display: none;
}

.header-items-call-donate {
  display: none;
}

.menu-button {
  border: none;
  opacity: 0;
  background-color: transparent;
  animation-name: lightsOn2;
  animation-duration: 1s;
  animation-delay: 1.5s;
  animation-fill-mode: forwards;
}

@media only screen and (min-width: 768px) {
  .header-container {
    height: 200px;
  }

  .trp-logo-container {
    height: 120px;
    width: 120px;
  }

  .trp-logo-container svg {
    height: 120px;
    width: 120px;
  }
}

@media only screen and (min-width: 1200px) {
  .menu-button {
    display: none;
  }

  .header-nav-bar {
    justify-content: flex-start;
  }

  .header-items-container {
    opacity: 0;
    display: inline-block;
    margin: 0 50px;
    height: 150px;
    width: 60%;
    animation-name: lightsOn3;
    animation-duration: 1s;
    animation-delay: 1.5s;
    animation-fill-mode: forwards;
  }

  .header-items-call-donate {
    position: absolute;
    /* top: 20px; */
    bottom: 30px;
    right: 40px;
    width: 150px;
    display: flex;
    flex-direction: column;
    opacity: 0;
    margin-left: 10px;
    animation-name: lightsOn3;
    animation-duration: 1s;
    animation-delay: 1.5s;
    animation-fill-mode: forwards;
    justify-content: space-evenly;
    justify-items: center;
  }

  a .phone-button {
    width: 100%;
    border: none;
    background-color: #005987;
    color: #f4d90c;
    border: 1px solid rgba(244, 217, 12, 0.5);
    font-family: lemon-milk;
    text-align: center;
    /* line-height: 50px; */
    padding: 0 10px;
    margin: 5px 0;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    border-radius: 5px;
    cursor: pointer;
  }

  .donate-button button {
    width: 100%;
    color: #005987;
    font-family: lemon-milk;
    text-align: center;
    line-height: 50px;
    background-color: transparent;
    border: none;
    background-color: #f4d90c;
    letter-spacing: 0.3em;
    font-weight: 600;
    border-radius: 5px;
    cursor: pointer;
  }

  @keyframes lightsOn3 {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  .header-items-container ul {
    /* height: 90%; */
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
    list-style-type: none;
    position: relative;
    top: 0;
  }

  .header-items-container ul a {
    color: inherit;
    height: 150px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    text-decoration: none;
  }

  .header-items-container ul li {
    width: 100%;
    text-align: center;
    position: relative;
    margin: 0 20px;
    font-family: lemon-milk;
    font-size: 1.2em;
    color: #f4d90c;
    /* align-self: flex-end; */
  }

  .header-items-container ul li:hover .drop-down-menu > li {
    display: block;
    top: 0;
  }

  .drop-down-menu li {
    display: none;
    position: absolute;
    top: 0;
    margin: 0 !important;
    color: #f4d90c !important;
    font-size: 16px !important;
    font-family: 'Barlow', 'san-serif' !important;
    font-weight: 400;
    border-radius: 0 0 10px 0;
    /* margin: 20px 0 !important; */
    padding: 20px 0 !important;
    transition: background-color 0.2s ease-in-out;
    /* height: 20px !important; */
  }

  .drop-down-menu li a {
    height: 100% !important;
  }

  .drop-down-menu li:hover {
    background-color: #0090d8;
  }

  .drop-down-menu li a:hover {
    font-weight: 600;
  }

  .drop-down-menu {
    display: flex;
    flex-direction: column !important;
    text-align: center;
    position: absolute;
    width: 100%;
    background-color: #005a87f3;
    border-radius: 0 0 10px 0;
  }

  .menu-active {
    font-size: 1.3em !important;
    color: #ffe100 !important;
    padding: 0 5px;
    border-radius: 2px;
    transition: font-size 0.5s ease-in-out;
  }
}

@media only screen and (min-width: 1400px) {
  .header-items-container {
    width: 65%;
  }
}
